.page__body {
  margin: 0;
}

.stopwatch {
  background-color: #282828;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.stopwatch__dial {
  box-sizing: border-box;
  background-color: #282828;
  border: 10px solid #282828;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
  box-shadow: -7px -7px 20px #2e2e2ee6, -4px -4px 15px #2e2e2ee6, 7px 7px 20px #0003, 4px 4px 15px #0003, inset -15px -15px 15px #2e2e2ee6, inset 15px 15px 15px #0003, inset -4px -4px 10px #2e2e2ee6, inset 4px 4px 10px #0003;
}

.stopwatch__second-hand, .stopwatch__minute-hand {
  transform-origin: bottom;
  animation-name: rotate360;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%)rotate(0);
}

.stopwatch__second-hand {
  background-color: #cd8142;
  border-radius: 3px;
  width: 1%;
  height: 40%;
  animation-duration: 5s;
}

.stopwatch__minute-hand {
  background-color: #69a6f8;
  border-radius: 6px;
  width: 2%;
  height: 25%;
  animation-duration: 60s;
}

.stopwatch__center {
  background-color: #4e4c64;
  border-radius: 50%;
  width: 3%;
  height: 3%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stopwatch__hour-mark {
  transform-origin: bottom;
  width: 1%;
  height: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%)rotate(0);
}

.stopwatch__hour-mark--1 {
  transform: translate(-50%, -100%)rotate(30deg);
}

.stopwatch__hour-mark--2 {
  transform: translate(-50%, -100%)rotate(60deg);
}

.stopwatch__hour-mark--3 {
  transform: translate(-50%, -100%)rotate(90deg);
}

.stopwatch__hour-mark--4 {
  transform: translate(-50%, -100%)rotate(120deg);
}

.stopwatch__hour-mark--5 {
  transform: translate(-50%, -100%)rotate(150deg);
}

.stopwatch__hour-mark--6 {
  transform: translate(-50%, -100%)rotate(180deg);
}

.stopwatch__hour-mark--7 {
  transform: translate(-50%, -100%)rotate(210deg);
}

.stopwatch__hour-mark--8 {
  transform: translate(-50%, -100%)rotate(240deg);
}

.stopwatch__hour-mark--9 {
  transform: translate(-50%, -100%)rotate(270deg);
}

.stopwatch__hour-mark--10 {
  transform: translate(-50%, -100%)rotate(300deg);
}

.stopwatch__hour-mark--11 {
  transform: translate(-50%, -100%)rotate(330deg);
}

.stopwatch__hour-mark--12 {
  transform: translate(-50%, -100%)rotate(360deg);
}

.stopwatch__hour-mark:after {
  content: "";
  background-color: #bdbdc2;
  height: 10%;
  margin-top: 10px;
  display: block;
}

.stopwatch__hour-mark:nth-child(3n):after {
  background-color: #44fafb;
}

@keyframes rotate360 {
  from {
    transform: translate(-50%, -100%)rotate(0);
  }

  to {
    transform: translate(-50%, -100%)rotate(360deg);
  }
}

